*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Signika', sans-serif;
}

html,
body {
  width: 100%;
}

.pokemon-list {
  display: block;
  overflow: hidden;
  text-align: center;
}

.pokemon-species {
  display: inline-block;
  position: relative;
  margin: 10px;
  padding: 10px;
  min-width: 150px;
  background-color: #EEEEEE;
  text-align: center;
  border-radius: 3px;
}

.pokemon-species-number {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 1;
  color: #CCCCCC;
  font-size: 20px;
}

.pokemon-species-img {
  position: relative;
  z-index: 2;
}

.pokemon-species-title {
  font-size: 16px;
  margin: 0 0 10px;
  text-transform: capitalize;
}

.loading {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100px;
  height: 100px;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

.loading svg {
  -webkit-animation: pokeball-spin 0.8s infinite;
          animation: pokeball-spin 0.8s infinite;
}

/* types */
ul:not(.pokemon-detail--abilities):not(.pokemon-detail--stats) {
  padding: 0;
}

[class*="pill-"] {
  display: inline-block;
  padding: 3px 5px;
  border-radius: 3px;
  list-style: none;
  margin-right: 8px;
  text-transform: capitalize;
}

.pill-normal {
  color: #0C0C0C;
  background-color: #EEEEEE;
}

.pill-electric {
  color: #0C0C0C;
  background-color: yellow;
}

.pill-fighting {
  color: #FFFFFF;
  background-color: brown;
}

.pill-psychic {
  color: #FFFFFF;
  background-color: purple;
}

.pill-dark {
  color: #FFFFFF;
  background-color: grey;
}

.pill-ghost {
  color: #FFFFFF;
  background-color: blueviolet;
}

.pill-grass {
  color: #FFFFFF;
  background-color: green;
}

.pill-fire {
  color: #FFFFFF;
  background-color: red;
}

.pill-water {
  color: #0C0C0C;
  background-color: aqua;
}

.pill-steel {
  color: #0C0C0C;
  background-color: silver;
}

.pill-fairy {
  color: #FFFFFF;
  background-color: salmon;
}

.pill-flying {
  color: #0C0C0C;
  background-color: skyblue;
}

.pill-rock {
  color: #0C0C0C;
  background-color: gainsboro;
}

.pill-bug {
  color: #0C0C0C;
  background-color: greenyellow;
}

.pill-ground {
  color: #0C0C0C;
  background-color: burlywood;
}

.pill-dragon {
  color: #FFFFFF;
  background-color: navy;
}

.pill-poison {
  color: #FFFFFF;
  background-color: rgb(92, 0, 128);
}

.pill-ice {
  color: #0C0C0C;
  background-color: rgb(137, 236, 253);
}

.btn {
  display: inline-block;
  color: #0C0C0C;
  background-color: #BBBBBB;
  border-radius: 3px;
  margin-right: 10px;
  padding: 5px 8px;
  text-decoration: none;
}

.btn-right {
  float: right;
}

.pokemon-detail {
  display: block;
  max-width: 600px;
  margin: 20px auto;
}

.pokemon-detail-heading {
  text-transform: capitalize
}

.pokemon-stats-wrapper,
.pokemon-types-wrapper,
.pokemon-detail-weak-wrapper,
.pokemon-detail-strength-wrapper {
  display: inline-block;
  width: 50%;
  vertical-align: top;
}

.pokemon-detail-info-wrapper,
.pokemon-detail-special-wrapper {
  display: block;
  width: 100%;
  border: 2px solid #EEEEEE;
  border-width: 2px 0;
  padding: 20px 0;
  margin: 20px 0 30px;
  clear: both;
}

.pokemon-detail-special-wrapper {
  border-bottom: 0;
  margin-bottom: 0;
  padding-bottom: 0;
}



@-webkit-keyframes pokeball-spin {
  from {
      -webkit-transform:rotate(0deg);
              transform:rotate(0deg);
  }
  to {
      -webkit-transform:rotate(360deg);
              transform:rotate(360deg);
  }
}



@keyframes pokeball-spin {
  from {
      -webkit-transform:rotate(0deg);
              transform:rotate(0deg);
  }
  to {
      -webkit-transform:rotate(360deg);
              transform:rotate(360deg);
  }
}



